import bus from '../common/bus';
export default {
    data() {
        return {
            collapse: false,
            items: [
                {
                    icon:'el-icon-star-on',
                    index:'1',
                    title:'信息发布',
                    subs: [
                        {
                            index: 'show',
                            title: '通知公告'
                        },
                        {
                            index: 'moneyList',
                            title: '财务学堂'
                        },
                        {
                            index: 'policyList',
                            title: '公务卡办理'
                        },{
                            index: 'confrimList',
                            title: '来款认领'
                        },{
                            index: 'budgetList',
                            title: '预算指南'
                        },{
                            index: 'guideList',
                            title: '科研指南'
                        }
                    ]
                },
                {
                    icon: 'el-icon-date',
                    index: '3',
                    title: '系统管理',
                    subs: [
                        {
                            index: 'userManegement',
                            title: '用户管理'
                        },
                        {
                            index: 'authority',
                            title: '权限管理'
                        },
                        {
                            index: 'basicInformation',
                            title: '基本信息'
                        },{
                            index: 'pass',
                            title: '修改密码'
                        }
                    ]
                }
            ]
        }
    },
    computed:{
        onRoutes(){
            return this.$route.path.replace('/','');
        }
    },
    created(){
        // 通过 Event Bus 进行组件间通信，来折叠侧边栏
        bus.$on('collapse', msg => {
            this.collapse = msg;
        })
    }
}



